#custom-text {
  background: white;
  border-radius: 2px;
  margin-bottom: rem(24px);
  padding: rem(50px) rem(50px);
  text-align: center;
  h3 {
    text-transform: uppercase;
    color: $gray-darker;
    font-size: rem(25px);
    font-weight: 700;
  }
  p {
    color: $gray-darker;
    font-weight: 400;
    font-size: 1.1em;
    .dark {
      color: $gray;
      font-weight: 400;
    }
  }
}
.page-content.page-cms{
  background: white;
  padding: rem(50px);
  text-align: justify; 
  font-family: $brand-font;
  font-weight: 300;
  width: 90%;
  margin: auto;
  line-height: rem(28px);
  .cms-box{
    img{
      max-width: 100%;
    }
  }
  h2{
  	text-transform: uppercase;
    padding-bottom: 20px;
    width: 100%;
    display: inline-block;
    border-bottom: 1px solid $border-color;
    margin-bottom: 20px;	  
  }
}
@include media-breakpoint-down(md) {
  #block-cmsinfo {
    padding: $medium-space $large-space;
  }
}



