.contact-rich {
  color: $gray-dark;
  margin-bottom: rem(32px);
  font-family: $brand-font;
  font-weight: 300;
  color: $gray-dark;
  h4 {
    text-transform: uppercase;
    color: $gray-darker;
    margin-bottom: rem(32px);
  }
  .block {
    height: auto;
    overflow: hidden;
    .icon {
      float: left;
      width: rem(56px);
      i {
        font-size: rem(32px);
        color: $brand-primary;
      }
    }
    .data {
      color: $gray-darker;
      font-size: $font-size-sm;
      width: auto;
      overflow: hidden;
      &.email{
        padding-top: rem(6px);
      }
    }
  }
}
.contact-form {
  background: white;
  padding: rem(16px);
  color: $gray-dark;
  width: 100%;
  h3 {
    text-transform: uppercase;
    color: $gray-darker;
  }
  .form-control-label{
	  font-weight: 300;
  }
  .input-group-btn{
	  border:1px solid $brand-primary;
  }
}

#contact{
	padding: rem(50px);
	.card{
		border:0px;
	}
}


