.search-widget {
  display: inline-block;
  @include search-box;
  .search_widget__form {
	  position: fixed;
	  width: calc(50% - 145px);
	  height: 100%;
	  background: rgba(255,255,255,1);
	  left:0;
	  top:0;
	  border-left: 1px solid $border-menu;
	  @include transition(0.3s);
	  @include transform(translateX(-800px));
	  padding:14% 5%;
	  z-index: -99;
	  &.show-panel{
// 	  	left:290px;
@include transform(translateX(290px));
	  }
/*
	  &.mobile{
		  position: relative;
		  width: 100%;
		  height: auto;
	  }
*/
	  h2{
		  margin-top:18%;
		  text-transform: uppercase;
		  color: $brand-secondary;
		  font-size: rem(28px);
		  padding-bottom: 30px;
		  font-weight: 400;
	  }
	  .search_widget__form_close-wrap {
		    background: $brand-secondary;
		    padding: 18px;
		    position: absolute;
		    z-index: -9;
		    right: 0px;
		    top: 0px;
		    display: none;
		    .search_widget__form_close{
				background: url('close-cross.png') no-repeat;   
				background-size: cover;
				display:block;
				width:38px;
				height:38px;
				cursor:pointer;		
				@include transition(0.2s);
				&:hover{
					@include transform(rotate(180deg))
				}	    
		    }
		}
  }
  .search_widget__btn{
	  cursor: pointer;
  }
}
.header-top {
  .search-widget {
    form {
	    width: 100%;
	    margin: auto;
      input[type="text"] {
        min-width: inherit;
        width: 100%;
        background: white;
        border:2px solid $border-color;
        height: 70px;
        @include border-radius(3px);
      }
      button[type=submit]{
	      right:15px;
	      bottom:16px;
	      color: $brand-secondary;
      }
    }
  }
}
#checkout {
  #search_widget {
    display: none; /* Not ideal solution by allows to reuse same hooks/templates */
  }
}

#pagenotfound {
  .page-content {
    #search_widget {
      width: 100%;
    }
  }
}

/*** Responsive part ***/
@include media-breakpoint-down(md) {
  .header-nav{
	  z-index: 100;
	  background: white;
  }
  .header-top {
	  z-index: 100;
	  background: white;
    .search-widget {
      float: none;
      padding: 0 rem(0px);
      .search_widget__form {
	      position: absolute !important;
	      width: 100%;
	      height: auto;
	      top:0;
	      left:-800;
	      @include transform(translateX(0px));
	      padding: 15px;
	      border-top:1px solid $border-menu;
	      z-index: -99;
	      h2{
		      display: none;
	      }
	      &.show-panel{
	    	height: auto;
	    	top:100%;
	    	left:0;
	      }
      }
      form {
        margin: 0 auto;
        input[type="text"] {
          min-width: inherit;
          background: white;
        }
      }
    }
  }
}


